    :progress="cedi.progress"
    :name="cedi.nombre"
    :depto="cedi.depto"
    :days="cedi.dias_laborados"
    :m3="cedi.m3_persona"
    :bonus_p="cedi.bono_persona"
    :bonus_tot="cedi.bono_depto"
    :asis="cedi.asistencia"
    :extra="cedi.$_extra_m3"
    :bonus_prod="cedi.bono_productividad"
    :bonus_goals="cedi.bono_metas"
    />
</div>
</template>


<script>

import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Cedi',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'chofercedi',
            city: 'lapaz',
            userCode: this.$route.params.user
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
